      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex;
      -webkit-flex-direction: column;
      -ms-flex-direction: column;
      flex-direction: column;

      & > * {
        -webkit-flex: 1 1 auto;
        -ms-flex: 1 1 auto;
