  padding: 0 16px;
  width: 200px;
  height: 40px;
  cursor: pointer;
  background-color: "#F3F6FF";
  border: 2px solid #0d5ff9;
  margin-right: 16px;
  transition: background-color 200ms;
}

input[type="file"]::file-selector-button:hover {
